import './App.css';
import {Counter} from './components/Counter.js'
import {HockeyPunk} from './components/HockeyPunk'
import {Logo} from './components/Logo'
import {StatTable} from './components/StatTable'
import {ScoresToday} from './components/scores/ScoresToday'
import {Footer} from './components/Footer'
import React, { useState, useEffect } from 'react';
import {Helmet} from "react-helmet";
const axios = require('axios');

function App() {

  //–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∏–≥—Ä –∏ –≥–æ–ª–æ–≤ –Ω–∞ –Ω–∞—á–∞–ª–æ —Å–µ–∑–æ–Ω–∞ 2021/22
  let startGoal = 730;
  let startGames = 1197;
  let CurrentSeason = "20212022"

  const goalGretsky = 894;

  let [goalOvi, setOviGoals] = useState(null) //–≥–æ–ª—ã –æ–≤–µ—á–∫–∏–Ω–∞ –≤ —Ç–µ–∫—É—â–µ–º —Å–µ–∑–æ–Ω–∞
  let [gamesOvi, setOviGames] = useState(null) //–≥–æ–ª—ã –æ–≤–µ—á–∫–∏–Ω–∞ –≤ —Ç–µ–∫—É—â–µ–º —Å–µ–∑–æ–Ω–∞
  let [allGame, setAllGame] = useState(null)  //–¥–∞–Ω–Ω—ã–µ –æ –∏–≥—Ä–∞—Ö –æ–≤–µ—á–∫–∏–Ω–∞ –≤ —Ç–µ–∫—É—â–µ–º —Å–µ–∑–æ–Ω–∞
  let [isRuss, setRuss] = useState(false) //—è–∑—ã–∫ 

  let [IDLastGameWithGolas, setIdLastGameWithGolas] = useState(null) // id –∏–≥—Ä—ã —Å –ø–æ—Å–ª–µ–¥–Ω–∏–º –≥–æ–ª–æ–º
  let [aboutLastGameWithGolas, setAboutLastGameWithGolas] = useState(null) // –¥–∞–Ω–Ω—ã–µ –∏–≥—Ä—ã —Å –ø–æ—Å–ª–µ–¥–Ω–∏–º –≥–æ–ª–æ–º

  let [IDLastGameGolas, setIdLastWithGolas] = useState(null) // id –∏–≥—Ä—ã —Å –ø–æ—Å–ª–µ–¥–Ω–∏–º –≥–æ–ª–æ–º
  let [aboutLastGame, setAboutLastGame] = useState(null) // –¥–∞–Ω–Ω—ã–µ –∏–≥—Ä—ã —Å –ø–æ—Å–ª–µ–¥–Ω–∏–º –≥–æ–ª–æ–º
  
  //let isRuss = false

  useEffect(()=> {
  
  //–æ–ø—Ä–µ–ª–µ—è—é —è–∑—ã–∫ –±—Ä–∞—É–∑–µ—Ä–∞
  var language = navigator.languages && navigator.languages[0] || // Chrome / Firefox
               navigator.language ||   // All browsers
               navigator.userLanguage;
  if (language.includes('ru')) { 

    setRuss(true)
  }
  
// –∑–∞–ø—Ä–æ—Å –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≥–æ–ª–æ–≤ –≤ —Ç–µ–∫—É—â–µ–º —Å–æ–∑–æ–Ω–µ –∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∏–≥—Ä
  axios.get('https://statsapi.web.nhl.com/api/v1/people/8471214/stats?stats=statsSingleSeason&season='+CurrentSeason)
  .then(function (response) {
    setOviGoals(startGoal+response.data.stats[0].splits[0].stat.goals)
    setOviGames(startGames+response.data.stats[0].splits[0].stat.games)
  })
  
//–∑–∞–ø—Ä–æ—Å –Ω–∞ –º–∞—Å—Å–∏–≤ –∏–≥—Ä –≤ —Ç–µ–∫—É—â–µ–º —Å–µ–∑–æ–Ω–µ
axios.get('https://statsapi.web.nhl.com/api/v1/people/8471214/stats?stats=gameLog&season='+CurrentSeason)
.then((response) => {
  let data = response;
  //console.log(data);
  
  

  setAllGame(data);
  
  let Game = data.data.stats[0].splits.find(i => i.stat.goals>0);

  setAboutLastGameWithGolas(Game)
  setIdLastGameWithGolas(Game.game.gamePk);

  setIdLastWithGolas(data.data.stats[0].splits[0].game.gamePk)
  setAboutLastGame(data.data.stats[0].splits[0])


});
  },[])
 

  


  //—Ñ—É–Ω–∫—Ü–∏—è —Å–∫–ª–æ–Ω—è–µ—Ç —Ü–∏—Å–ª–∏—Ç–µ–ª—å–Ω—ã–µ
  let num_word = (value) => { 
  let words = ['—à–∞–π–±–∞', '—à–∞–π–±—ã', '—à–∞–π–±'];
  value = Math.abs(value) % 100; 
  let num = value % 10;
  if(value > 10 && value < 20) return words[2]; 
  if(num > 1 && num < 5) return words[1];
  if(num == 1) return words[0]; 
  return words[2];
  }
  
  let difference = goalGretsky - goalOvi

  return (
    <div className="App">
           {isRuss ? 
           <Helmet>
                <meta charSet="utf-8" />
                <title>–û–≤–µ—á–∫–∏–Ω –ê–ª–µ–∫—Å–∞–Ω–¥—Ä –≤ –ù–•–õ, –≥–æ–ª—ã —Å–µ–≥–æ–¥–Ω—è, —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞, —Å—á–µ—Ç—á–∏–∫ –≥–æ–ª–æ–≤, —Ç–∞–±–ª–∏—Ü–∞, —Ä–µ–∫–æ—Ä–¥ –ª—É—á—à–µ–≥–æ –±–æ–º–±–∞—Ä–¥–∏—Ä–∞ –ì—Ä–µ—Ü–∫–∏</title>
                <link rel="canonical" href="http://mysite.com/example" />
            </Helmet> :
            <Helmet>
                <meta charSet="utf-8" />
                <title>Ovechkin Alex scores NHL, goals, statistic, record, Gretzky, counter</title>
                <link rel="canonical" href="http://mysite.com/example" />
            </Helmet>
}
      <header>
      </header>
      <main className="App-header">
        <Logo/>
        <Counter isRuss={isRuss} difference={difference} num_word={num_word}/>
        <HockeyPunk difference={difference}/>
        <ScoresToday isRuss={isRuss} num_word={num_word} allGame={allGame} IDLastGameWithGolas={IDLastGameWithGolas} aboutLastGameWithGolas={aboutLastGameWithGolas}
        IDLastGameGolas={IDLastGameGolas} aboutLastGame={aboutLastGame}/>
        <StatTable isRuss={isRuss} goalOvi={goalOvi} gamesOvi={gamesOvi}/>
      </main>
      <footer className="footer">
      <Footer isRuss={isRuss}/>
       </footer>
    </div>
  );
}

export default App;